@media (max-width: 900px){
  .PressBanner{
      display: none;
  }

  .PressBannerMobile{
      display: inline;
  }
}

@media (min-width: 900px){
  .PressBanner{
      display: inline;
  }

  .PressBannerMobile{
      display: none;
  }
}

.container {
  text-align: center;
}

.header {
  text-align: center;
  margin-top: 30px;
  padding-bottom: 30px;
}

.PressBanner {
  width: 100%;
}

.PressBannerMobile {
  width: 100%;
}

.PressImgDiv {
    display: inline-block;
}
  
.PressImgDiv a {
  display: block;
}

.PressImgDiv img {
  width: 100%;
  height: 100%;
  object-fit: fill;
}
  
@media (min-width: 1199px) {
  .PressImgDiv {
    width: 20%;
    margin: 5px;
  }
}

@media (max-width: 1199px) {
  .PressImgDiv {
    width: 25%;
    margin: 5px;
  }
}

@media (max-width: 767px) {
  .PressImgDiv {
    width: 40%;
    /* 80% if you want a single one */
  }
}

@media (max-width: 420px) {
  .PressImgDiv {
    width: 80%;
    /* 80% if you want a single one */
  }
}