.FoundingMembers {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.LaunchPartners {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.PlatformLaunchPartnersBanner{
    width: 100%;
}

.PlatformLaunchPartnersBannerMobile{
    width: 100%;
}

.PartnerImage {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 150px;
    background-color: #27272A;
    border-radius: 50px;
}

.PartnerImage.Group a {
    display: inline-flex;
}

.PartnerImage.Group a img:first-child {
    height: 4.625rem;
    width: 4.625rem;
}

.PartnerImage.Group a img:last-child {
    height: 74px;
    width: 211px;
}

.FoundingMemberImage {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 150px;
    background-color: #27272A;
    border-radius: 50px;
}

.Partner p {
    margin: 10px 10px 20px 10px;
}

.FoundingMember p {
    margin: 10px 10px 20px 10px;
}

.PageBodyAltColor {
    margin: 75px 0 75px 0;
}

.TrustedBy {
    width: 90%;
}

