@media (max-width: 900px){
    .PlatformLaunchPartnersBanner{
        display: none;
    }

    .PlatformLaunchPartnersBannerMobile{
        display: inline;
    }
}

@media (min-width: 900px){
    .PlatformLaunchPartnersBanner{
        display: inline;
    }

    .PlatformLaunchPartnersBannerMobile{
