.POWtoPOS {
    width: 100%;
}

.Web3Blurb {
    margin-top: 20px;
    margin-bottom: 80px;
}

.MeetTheTeam {
    font-size: 70px;
    padding-top: 50px;
}

.ECPDevelopmentTeam {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.ECPLaunchPartners {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.ECPAdvisors {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.ECPDevelopmentMember {
    height: 300px;
    width: 300px;
    padding: 30px 60px 30px 60px;
}

.TeamDescription {
    padding-top: 50px;
}

.HeaderPadding {
    padding-top: 50px;
    padding-bottom: 20px;
}

@media (max-width: 900px) {
    .CarbonFootprintBanner {
        display: none;
    }

    .CarbonFootprintBannerMobile {
        display: inline;
    }

    .ClimateGraphics{
        display: none;
    }

    .EthereumMergeMobile {
        display: inline;
        width: 100%;
        padding-top: 30px;
    }

    .EthereumMergeMobileGraphic {
        display: inline;
        width: 100%;
    }

    .EthereumClimateMobile {
        display: inline;
        width: 100%;
        padding-top: 60px;
    }

    .EthereumClimateMobileGraphic {
        display: inline;
        width: 100%;
    }
}

@media (min-width: 900px) {
    .CarbonFootprintBanner {
        display: inline;
    }

    .CarbonFootprintBannerMobile {
        display: none;
    }
    
    .ClimateGraphics{
        display: inline;
        padding-bottom: 75px;
    }

    .EthereumMergeMobile {
        display: none;
    }

    .EthereumMergeMobileGraphic {
        display: none;
    }

    .EthereumClimateMobile {
        display: none;
    }

    .EthereumClimateMobileGraphic {
        display: none;
    }
}

.CarbonFootprintBanner {
    width: 100%;
}

.CarbonFootprintBannerMobile {
    width: 100%;
}

.ClimateGraphics {
    padding-top: 100px;
    width: 100%;
}