.YoutubeUNFCCC{
    margin-top: 50px;
    margin-bottom: 20px;
}

@media (max-width: 900px){
    .HeroBanner{
        display: none;
    }

    .HeroBannerMobile{
        display: inline;
    }
}

@media (min-width: 900px){
    .HeroBanner {
        display: inline;
    }

    .HeroBannerMobile {
        display: none;
    }
}

@media (min-width: 1400px){
    .YoutubeUNFCCC {
        width: 1120px; 
        height: 630px;
    }
}

@media (max-width: 1400px){
    .YoutubeUNFCCC {
        width: 840px; 
        height: 472.5px;
    }
}

@media (max-width: 1200px){
    .YoutubeUNFCCC {
        width: 640px; 
        height: 354.375px;
    }
}

@media (max-width: 800px){
    .YoutubeUNFCCC {
        width: 560px; 
        height: 315px;
    }
}

@media (max-width: 700px){
    .YoutubeUNFCCC {
        width: 420px; 
        height: 236.25px;
    }
}

@media (max-width: 570px){
    .YoutubeUNFCCC {
        width: 280px; 
        height: 157.5px;
    }
}

@media (max-width: 345px){
    .YoutubeUNFCCC {
        width: 210px; 
        height: 118.125px;
    }
}

.HeroBanner {
    width: 100%;
}

.HeroBannerMobile {
    width: 100%;
}

@media (min-width: 800px){

    .TrustedBy {
        margin-top: 30px;
        display: inline;
    }

    
    .TrustedByMobile {
        display: none;
    }

    .Union {
        width: 15%;
    }

    .Hands {
        width: 20%;
    }

}

@media (max-width: 800px){

    .TrustedBy {
        display: none;
    }

    .TrustedByMobile {
        margin-top: 30px;
        width: 90%;
        display: inline;
    }

    .Union {
        width: 40%;
    }

    .Hands {
        width: 55%;
    }
}

