.VideoButton {
    background: #14B8A6;
    border-radius: 999px;
    width: 150px;
    padding: 1.1em;
    -webkit-tap-highlight-color: transparent;
    text-align: center;
    margin: auto;
    margin-top: 3em;
    border: 0px
}

.VideoButton:hover {
    background-color: #3B82F6;
}

.VideoButton a:visited { 
    text-decoration: none; 
}

.VideoButton a:link { 
    text-decoration: none; 
}

.VideoButton a {
    font-weight: bold;
    color: white;
    font-size: 16px;
}