ul {
    margin:0px;
    padding:0px;
}

.navLinksMobile a {
  text-align: center;
}
  
.logo { 
  max-height: 2.8em;
  margin-top: .3em;
}

.Nav {
  z-index: 100;
}

.navigation {
    background: var(--headerandfootercolor);
}

.navigation a:not(.logolink){
  text-decoration: none;
  display: block;
  padding: 1em;
  color: white;
  -webkit-tap-highlight-color: transparent;
}

.navigation a:hover:not(.logolink){
  background: #1565C0;
  backface-visibility: hidden;
}
  
@media (min-width: 900px){
  .logo {
    padding-right: 1em;
  }

  .navigation {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    list-style: none;
    min-width: 220px;
  }

  .navigation a { 
    text-align: center; 
    padding: 10px;
    padding-bottom: 1px;
    padding-top: 1px;
  }

  .Nav {
    position: sticky;
    top: 0;
  }
  
  .hamburgerMenu {
    display:none!important;
  }

  .navLinksMobile {
    display:none!important;
  }

  .invisible {
    display:none!important;
  }
}

@media (max-width: 899px){
  .navLinksDesktop {
    display:none!important;
  }

  .mobileFormat{
    display: flex;
    justify-content: space-between;
  }
  
  .navLinksMobile{
    text-align: center;
  }

  .Nav {
    position: sticky; 
    top: 0;
  }

  .invisible {
    height: 35px;
    width: 35px;
    padding-right: 5px;
    padding-top: 2px;
  }

  .hamburgerMenu {
    height: 35px;
    width: 35px;
    padding-left: 5px;
    padding-top: 2px;
  }
}