@media (max-width: 900px){
    .JoinTheInitiativeBanner{
        display: none;
    }

    .JoinTheInitiativeBannerMobile{
        display: inline;
    }
}

@media (min-width: 900px){
    .JoinTheInitiativeBanner{
        display: inline;
    }

    .JoinTheInitiativeBannerMobile{
        display: none;
    }
}

.JoinTheInitiativeBanner {
    width: 100%;
}

.JoinTheInitiativeBannerMobile{
    width: 100%;
}

.JoinButton {
    background: #14B8A6;
    border-radius: 999px;
    width: 150px;
    height: 25px;
    padding: 1em;
    -webkit-tap-highlight-color: transparent;
    text-align: center;
    margin: auto;
    margin-top: 3em;
    margin-bottom: 3em;
}

.JoinButton:hover {
    background-color: #3B82F6;
}

.JoinButton a:visited { 
    text-decoration: none; 
}

.JoinButton a:link { 
    text-decoration: none; 
}

a.JoinButton {
    font-weight: bold;
    color: white;
}

.JoinButton a {
    font-weight: bold;
    color: white;
}

#reactHubspotForm1:not(no-list hs-error-msgs inputs-list){
    background-color: var(--bodyaltcolor);
    color: white;
    text-size-adjust: auto;
    padding: 0 60px 0 60px;
}

@media (min-width: 460px) {
    #reactHubspotForm1:not(no-list hs-error-msgs inputs-list){
        margin: 20px -70px 20px -70px;
        padding: 0 60px 0 60px;
    }
}

@media (max-width: 460px) {
    #reactHubspotForm1:not(no-list hs-error-msgs inputs-list){
        padding: 0 60px 0 60px;
    }
}

#reactHubspotForm1 ul {
    list-style: none;
}

#reactHubspotForm1 li {
    padding-top: 10px;
}

#reactHubspotForm1 .input {
    padding-top: 5px;
    padding-bottom: 20px;
}

.hs-error-msg {
    color: red;
}

.hs-error-msgs {
    padding-bottom: 25px;
}

.hs-form-private {
    display: inline-block;
    justify-content: center;
    align-items: center;
    padding: 20px 0 40px 0;
}

.HubspotFormContainer {
    display: inline-block;
    justify-content: center;
    align-items: center;
}

.hs-button {
    background: #14B8A6;
    border-radius: 999px;
    width: 150px;
    height: 50px;
    padding: 1em;
    -webkit-tap-highlight-color: transparent;
    text-align: center;
    margin: auto;
    color: white;
    font-weight: bold;
}

.form-columns-0{
    visibility: hidden;
}