:root {
    --headerandfootercolor: #27272A;
    --textcolor: #A1A1AA;
    --bodyaltcolor: #3F3F46;
}

html {
    background: var(--headerandfootercolor);
}

.container {
    background-color: #18181B;
                     /*#141c2e;*/
                     /*#413d44;*/
}

p {
    color: var(--textcolor);
}

h1 {
    color: var(--textcolor);
}

h2 {
    color: var(--textcolor);
  }
  
h3 {
    color: var(--textcolor);
  }

.FooterMoon {
    opacity: .5;
    width: 100%;
    height: 50%;
    position: static;
}

@media (min-width: 800px) {
    .PageBody {
        margin: 0 175px 0 175px;
    }

    .PageBodyAltColor {
        border-left: 175px;
        border-right: 175px;
        border-bottom: 200px;
        background-color: var(--bodyaltcolor);
    }

    .PageBodyAltColor h2 {
        margin: 0 30px 0 30px;
    }
}